
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Upload from '@/components/Upload'
import { mapActions } from 'vuex'
let heights = []
let weights = []
for (var i = 150; i <= 200; i++) {
heights.push({
name: i,
value: i
})
}
for (var k = 40; k <= 100; k++) {
weights.push({
name: k,
value: k
})
}
export default {
components: {
Group,
XInput,
PopupPicker,
XAddress,
Datetime,
Upload,
XButton
},
data () {
return {
o: {
a: 1,
b: 2
},
form: {
nickname: '',
sex: [],
birthday: '',
stature: [],
weight: [],
adsValue: ['330000', '330200'],
autograph: '',
headimgurl: ''
},
sexs: [[{
name: '男',
value: '1'
}, {
name: '女',
value: '2'
}]],
heights: [heights],
weights: [weights],
addressData: ChinaAddressData,
loading: false
}
},
created () {
let userInfo = Object.assign({}, this.$store.getters['user/userInfo'])
if (Object.keys(userInfo).length === 0) {
this.getUserInfo().then((res) => {
res.sex = [res.sex.toString()]
res.weight = [res.weight.toString()]
res.stature = [res.stature.toString()]
this.form = Object.assign({}, res)
})
} else {
userInfo.sex = [userInfo.sex.toString()]
userInfo.weight = [userInfo.weight.toString()]
userInfo.stature = [userInfo.stature.toString()]
this.form = Object.assign({}, userInfo)
}
},
methods: {
...mapActions('user', [
'getUserInfo',
'saveUserInfo'
]),
afterClip (base64) {
this.form.headimgurl = base64
},
saveInfo () {
this.$validator.validateAll().then(result => {
console.log(result)
let getErrors = this.vErrors.all()
if (getErrors.length > 0) {
this.$vux.toast.show({
text: getErrors[0],
position: 'bottom',
type: 'text',
time: 1500,
width: '10em'
})
console.log(getErrors[0])
} else {
this.loading = true
const formData = Object.assign({}, this.form)
formData.weight = formData.weight.join()
formData.sex = formData.sex.join()
formData.stature = formData.stature.join()
this.saveUserInfo(formData).then((res) => {
this.$vux.toast.show({
text: '保存成功',
position: 'bottom',
type: 'text',
time: 1500,
width: '10em'
})
this.loading = false
})
}
})
}
}
}



